<template>
  <div class="document-main">
    <ul class="document-ul">
      <router-link to="/document" exact tag="li" active-class="document-ul-active">
        <a>html</a>
      </router-link>
      <router-link to="/document/css" tag="li" active-class="document-ul-active">
        <a>css</a>
      </router-link>
      <router-link to="/document/js" tag="li" active-class="document-ul-active">
        <a>js</a>
      </router-link>
      <router-link to="/document/vuecli" tag="li" active-class="document-ul-active">
        <a>vue-cli</a>
      </router-link>
      <router-link to="/document/node" tag="li" active-class="document-ul-active">
        <a>node</a>
      </router-link>
      <router-link to="/document/xcx" tag="li" active-class="document-ul-active">
        <a>小程序</a>
      </router-link>
      <router-link to="/document/python" tag="li" active-class="document-ul-active">
        <a>pyth</a>
      </router-link>
      <router-link to="/document/php" tag="li" active-class="document-ul-active">
        <a>php</a>
      </router-link>
      <router-link to="/document/java" tag="li" active-class="document-ul-active">
        <a>java</a>
      </router-link>
  </ul>
  <transition mode="out-in">
    <router-view class="main-center"></router-view>
  </transition>
  </div>
</template>
<script>
export default {
  name: 'home'
}

</script>
<style>
.v-enter{
  opacity: 0;
}
.v-enter-to{
  opacity: 1;
}
.v-enter-active,.v-leave-active{
  transition: 1s;
}
.v-leave{
  opacity: 1;
}
.v-leave-to{
  opacity: 0;
}
</style>



